<template>
  <v-container>
    <v-card class="transparent" flat v-if="subjects.length > 0">
      <v-card-title>
        <h2 class="ma-auto text-center">Zoom Batch</h2>
      </v-card-title>
      <v-card-text>
        <v-list class="text-center transparent">
          <v-list-item
            v-for="(s, i) in subjects"
            :key="i"
            link
            class="segment"
            :to="'/zoombatch/' + s.link"
          >
            <v-list-item-content
              ><h3>{{ s.title }}</h3>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-card-text>
    </v-card>
    <div class="text-center my-4" v-else>
      <v-progress-circular
        :rotate="360"
        :size="120"
        :width="10"
        indeterminate
        color="orange"
      ></v-progress-circular>
    </div>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      subjects: [],
    };
  },
  methods: {
    getSubjects() {
      fetch(
        "https://script.google.com/macros/s/AKfycbzxQATfWCrTK8aD1PYEGfUWpVwX7Pxh_yy31BeCtc_KkS4WzSm-xjA-rUvRYzd4PmXH7w/exec?type=list"
      )
        .then((res) => res.json())
        .then((data) => {
          this.subjects = data;
        });
    },
  },
  created() {
    this.getSubjects();
  },
};
</script>

<style lang="scss" scoped>
.segment {
  border-radius: 17px;
  background-image: linear-gradient(
    to right top,
    #53e7a0,
    #49e8a2,
    #3de9a4,
    #2deaa7,
    #12eba9
  );
  margin: 10px;
  &:hover {
    transform: scale(1.01);
    border: 2px solid rgb(25, 211, 65);
  }
}
</style>